body {
  font-family: sans-serif;
}

.animations-container {
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
}

.animation-container {
  position: relative;
  float: left;
  height: 88px;
  -webkit-perspective: 100px;
  -ms-perspective: 100px;
  -moz-perspective: 100px;
  -o-perspective: 100px;
  perspective: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  background: #1d1b36;
  color: #fff;
  border-radius: 6px;
}

.animation-container span {
  font-size: 11px;
  display: block;
  padding: 0 5px;
}

.animation {
  width: 30px;
  height: 30px;
  border-radius: 20%;
  background-color: #ff0081;
  border-radius: 50%;
}

@keyframes drop {
  0% {
    transform: translateY(-40px);
    animation-timing-function: ease-in;
    opacity: 0;
  }

  38% {
    transform: translateY(0);
    animation-timing-function: ease-out;
    opacity: 1;
  }

  55% {
    transform: translateY(-20px);
    animation-timing-function: ease-in;
  }

  72% {
    transform: translateY(0);
    animation-timing-function: ease-out;
  }

  81% {
    transform: translateY(-10px);
    animation-timing-function: ease-in;
  }

  90% {
    transform: translateY(0);
    animation-timing-function: ease-out;
  }

  95% {
    transform: translateY(-5px);
    animation-timing-function: ease-in;
  }

  100% {
    transform: translateY(0);
    animation-timing-function: ease-out;
  }
}

.drop {
  animation: drop 1s linear 0s 1 normal forwards;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fade-in {
  animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@keyframes fly-in-bottom {
  0% {
    transform: translateY(40px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.fly-in-bottom {
  animation: fly-in-bottom 0.5s forwards;
}

@keyframes fly-in-left {
  0% {
    transform: translateX(-40px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.fly-in-left {
  animation: fly-in-left 0.5s forwards;
}

@keyframes fly-in-right {
  0% {
    transform: translateX(40px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.fly-in-right {
  animation: fly-in-right 0.5s forwards;
}

@keyframes fly-in-top {
  0% {
    transform: translateY(-40px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.fly-in-top {
  animation: fly-in-top 0.5s forwards;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(0.7);
  }

  50% {
    transform: scale(1);
  }

  75% {
    transform: scale(0.7);
  }

  100% {
    transform: scale(1);
  }
}

.pulse {
  animation: pulse 1s ease-in-out both;
}

@keyframes rotate-in-left {
  0% {
    transform: rotate(-360deg) translateX(-40px);
    transform-origin: left;
  }

  100% {
    transform: rotate(0deg) translateX(0);
    transform-origin: left;
  }
}

.rotate-in-left {
  animation: rotate-in-left 0.8s ease-in-out both;
}

@keyframes rotate-in-right {
  0% {
    transform: rotate(360deg) translateX(40px);
    transform-origin: right;
  }

  100% {
    transform: rotate(0deg) translateX(0);
    transform-origin: right;
  }
}

.rotate-in-right {
  animation: rotate-in-right 1s ease-in-out both;
}

@keyframes twirl-in {
  0% {
    transform: rotate(-540deg) scale(0);
    opacity: 0;
  }

  100% {
    transform: rotate(0) scale(1);
    opacity: 1;
  }
}

.twirl-in {
  animation: twirl-in 0.6s ease-out both;
}

@keyframes whoosh-in-left {
  0% {
    transform: translateZ(-40px) translateX(-40px);
    opacity: 0;
  }

  100% {
    transform: translateZ(0) translateX(0);
    opacity: 1;
  }
}

.whoosh-in-left {
  animation: whoosh-in-left 0.5s ease-in-out both;
}

@keyframes whoosh-in-right {
  0% {
    transform: translateZ(-40px) translateX(40px);
    opacity: 0;
  }

  100% {
    transform: translateZ(0) translateX(0);
    opacity: 1;
  }
}

.whoosh-in-right {
  animation: whoosh-in-right 0.5s ease-in-out both;
}

@keyframes zoom-in {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(1.3);
    opacity: 1;
  }
}

.zoom-in {
  animation: zoom-in 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes zoom-out {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(0.7);
    opacity: 1;
  }
}

.zoom-out {
  animation: zoom-out 0.5s ease-in-out both;
}
